$(document).ready(function(){  
  // Navbar
  $('span').click(function(event){
    event.preventDefault();
    $('.navBar-list').toggleClass('navBar-active');
  });
  // navbar
  $(window).scroll(function(){
    let scrollTop = $(window).scrollTop();
    let navbarH = $('nav').height();
    if ( scrollTop >= 1 ) {
      $('nav').addClass('fixed')
      $('body').css('paddingTop', navbarH)
    } else {
      $('nav').removeClass('fixed')
      $('body').css('paddingTop', 0)
    }
  })
  // 
  $('.reserveBtn').click(function(e){
    e.preventDefault();
    $('.reserve-modal').fadeIn(500);
    $('.bg-opacity').addClass('active');
    $('.reserve-confirm').click(function(e){
      e.preventDefault();
      $('.reserve-modal').fadeOut(500);
      $('.modal-success').fadeIn(500);
    });
    $('.closeBtn').on('click', function() {
      e.preventDefault();
      $('.reserve-modal').fadeOut(300);
      $('.modal-success').fadeOut(300);
      setTimeout(() => {$('.bg-opacity').removeClass('active')}, 300);
    });
    $(function() {
      $('.chooseDate').daterangepicker({
        singleDatePicker: true,
        autoApply:true,
        locale: {
          format: 'YYYY-MM-DD',
          daysOfWeek:["","","","","","",""],
          monthNames: ["","","","","","","","","","","",""],
        },
      }, 
      function(start, end, label) {
      });
    });
  });
    
  // 
  $('.arrowBtn a').click(function(){
    event.preventDefault();
      $('html, body').animate({
        scrollTop :1400
      },100)
  })
  // QA
  $('.question li .answer ').click(function(e){
    e.preventDefault();
    $(this).parent().find('a').toggleClass('active');
    $(this).parent().find('p').slideToggle();
    $(this).parent().siblings().find('p').slideUp();
    $(this).parent().siblings().find('a').removeClass('active');
  })

  // datePicker
  $(function() {
    $('.chooseDate').daterangepicker({
      singleDatePicker: true,
      autoApply:true,
      locale: {
        format: 'YYYY-MM-DD',
        daysOfWeek:["","","","","","",""],
        monthNames: ["","","","","","","","","","","",""],
      },
    }, 
     function(start, end, label) {
    });
  });
});
